<template>
  <div>
    <div class="music-btns">
      <el-button size="small" icon="el-icon-refresh" circle @click="toggleBreakMusic" class="toggle-btn"></el-button>
      <el-button v-if="playingMusic" size="small" icon="el-icon-video-pause" circle @click="stopMusic" class="stop-btn"></el-button>
    </div>
    <div class="audios">
      <audio src="./static/audios/training/start.m4a" preload ref="start"></audio>
      <audio src="./static/audios/training/tick.mp3" preload ref="tick"></audio>
      <audio src="./static/audios/training/break.m4a" preload ref="rest"></audio>
      <audio src="./static/audios/training/end.m4a" preload ref="end"></audio>
      <audio src="./static/audios/training/set_start.m4a" preload ref="setStart"></audio>
      <audio src="./static/audios/training/set_end.m4a" preload ref="setEnd"></audio>
      <audio src="./static/audios/training/encourage0.m4a" preload ref="encourage0"></audio>
      <audio src="./static/audios/training/encourage1.m4a" preload ref="encourage1"></audio>
      <audio src="./static/audios/training/break_music0.mp3" preload loop ref="break_music0"></audio>
      <audio src="./static/audios/training/break_music1.mp3" preload loop ref="break_music1"></audio>
      <audio src="./static/audios/training/break_music2.mp3" preload loop ref="break_music2"></audio>
      <audio src="./static/audios/training/num1.m4a" preload ref="num1"></audio>
      <audio src="./static/audios/training/num2.m4a" preload ref="num2"></audio>
      <audio src="./static/audios/training/num3.m4a" preload ref="num3"></audio>
      <audio src="./static/audios/training/num4.m4a" preload ref="num4"></audio>
      <audio src="./static/audios/training/num5.m4a" preload ref="num5"></audio>
      <audio src="./static/audios/training/num6.m4a" preload ref="num6"></audio>
      <audio src="./static/audios/training/num7.m4a" preload ref="num7"></audio>
      <audio src="./static/audios/training/num8.m4a" preload ref="num8"></audio>
      <audio src="./static/audios/training/num9.m4a" preload ref="num9"></audio>
      <audio src="./static/audios/training/num10.m4a" preload ref="num10"></audio>
      <audio src="./static/audios/training/num20.m4a" preload ref="num20"></audio>
      <audio src="./static/audios/training/num30.m4a" preload ref="num30"></audio>
      <audio src="./static/audios/training/num40.m4a" preload ref="num40"></audio>
      <audio src="./static/audios/training/num50.m4a" preload ref="num50"></audio>
      <audio src="./static/audios/training/num60.m4a" preload ref="num60"></audio>
      <audio src="./static/audios/training/num70.m4a" preload ref="num70"></audio>
      <audio src="./static/audios/training/num80.m4a" preload ref="num80"></audio>
      <audio src="./static/audios/training/num90.m4a" preload ref="num90"></audio>
      <audio src="./static/audios/training/num100.m4a" preload ref="num100"></audio>
    </div>
    <el-form label-width="80px" class="training-form">
      <el-form-item  v-if="buildInPlans.length" label="内置计划">
        <span class="plan-item" v-for="(plan, index) in buildInPlans" :key="index">
          <el-button @click="selectPlan(plan)">{{ plan.name }}</el-button>
        </span>
      </el-form-item>
      <el-form-item label="快捷计划">
        <slot v-if="quickPlans.length">
          <span class="plan-item" v-for="(plan, index) in quickPlans" :key="index">
            <el-button @click="selectPlan(plan)">{{ plan.name }}</el-button>
            <el-button class="del-btn" type="danger" size="small" icon="el-icon-delete" circle @click.prevent="deletePlan(index)"></el-button>
          </span>
        </slot>
        <span v-else>暂无</span>
      </el-form-item>
      <el-divider></el-divider>
      <el-form-item
        v-for="(set, index) in trainingForm.sets"
        :label="'第' + (index + 1) + '组:'"
        :key="set.key" >
        <el-row :gutters="20">
          <el-col class="set-item">
            <span class="set-label">动作名称:</span> <el-input v-model="set.name" size="small" class="set-name"></el-input>
          </el-col>
          <el-col class="set-item">
            <span class="set-label">次数:</span> <el-input-number v-model="set.times" :min="1" :max="100" size="small" controls-position="right"></el-input-number> 次
          </el-col>
          <el-col class="set-item">
            <span class="set-label">每次时间:</span> <el-input-number v-model="set.interval" :min="1" size="small" controls-position="right"></el-input-number> 秒
          </el-col>
          <el-col class="set-item">
            <span class="set-label">休息时间:</span> <el-input-number v-model="set.rest" :min="0" size="small" controls-position="right"></el-input-number> 秒
          </el-col>
          <el-button type="text" @click.prevent="duplicateSet(set)">复制该组</el-button>
          <el-button type="danger" icon="el-icon-delete" size="small" circle @click.prevent="removeSet(set)"></el-button>
        </el-row>
      </el-form-item>
      <el-form-item>
        <el-button @click="addSet" size="small">添加新组</el-button>
        <el-button  @click="removeAllSets" size="small">清空所有</el-button>
      </el-form-item>
      <el-form-item label="总时长">
        {{ formatTotalTime }}
      </el-form-item>
      <el-form-item>
        <el-button :disabled="!trainingForm.sets.length" @click="startTraining" type="primary">开始训练</el-button>
        <el-button :disabled="!trainingForm.sets.length" @click="savePlan">存为快捷计划</el-button>
        <el-popover v-if="quickPlans.length"
          placement="top" v-model="popoverVisible"
          width="400"
          trigger="click">
          <div>
            <el-button class="plan-item" v-for="(plan, index) in quickPlans" :key="index" @click="saveAsPlan(plan, index)">{{ plan.name }}</el-button>
          </div>
          <el-button :disabled="!trainingForm.sets.length" slot="reference">另存为</el-button>
        </el-popover>
      </el-form-item>
    </el-form>

    <el-dialog class="training-dialog"
      :visible.sync="trainingDialogVisible"
      :fullscreen="true">
      <div class="training-info" v-if="trainingForm.sets.length">
        <div class="training-progress">
          <div class="progress-item" v-for="(set, i) in trainingForm.sets" :key="i" :style="`flex:${Math.floor(set.times * set.interval)}`">
            <span v-if="activeSet === i" class="done-bar" :class="isTraining ? 'done' : ''" :style="isTraining ? `animation:loadBar ${Math.floor(set.times * set.interval)}s linear 0s 1 normal;` : ''"></span>
            <span v-else class="done-bar" :class="activeSet > i ? 'done' : '' "></span>
          </div>
        </div>
        <div class="finished" v-if="finished">
          <div class="picture">🎉🎉🎉</div>
          <div class="text">恭喜你！完成训练！</div>
        </div>
        <div v-else class="current-set-info">
          <div class="set-name" v-if="isResting">下一个动作：{{trainingForm.sets[activeSet+1].name}}</div>
          <div class="set-name" v-else>{{trainingForm.sets[activeSet].name}}</div>
          <div v-if="isResting" class="remaining-rest">
            <i class="resting-progress" :style="`animation:riseUp ${trainingForm.sets[activeSet].rest}s linear 0s 1 normal;`"></i>
            resting {{currentTick}}s
          </div>
          <div v-else class="remaining-times">{{currentTick || trainingForm.sets[activeSet].times}}</div>
          <div class="operation-btns">
            <el-button v-if="isPaused" class="pause-btn" type="primary" icon="el-icon-video-play" circle @click="isPaused = false"></el-button>
            <el-button v-else class="pause-btn" type="primary" icon="el-icon-video-pause" circle @click="isPaused = true"></el-button>
          </div>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <!--<el-button @click="trainingDialogVisible = false">取 消</el-button>-->
        <el-button v-if="finished" type="primary" @click="trainingDialogVisible = false">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import buildInPlans from './workoutPlans'

export default {
  name: 'count',
  data () {
    return {
      trainingForm: {
        sets: []
      },
      totalTime: 0,
      timer: null,
      quickPlans: [],
      buildInPlans,
      popoverVisible: false,
      breakMusic: 'break_music0',
      playingMusic: false,
      trainingDialogVisible: false,
      activeSet: 0,
      isTraining: false,
      isResting: false,
      isPaused: false,
      currentTick: undefined,
      finished: false
    }
  },
  computed: {
    formatTotalTime: function () {
      return this.formatTime(this.totalTime)
    },
    totalWorkoutTime: function () {
      let sets = this.trainingForm.sets
      let totalWorkoutTime = 0;
      for (let i = 0; i < sets.length; i++) {
        totalWorkoutTime += Number(sets[i].times) * Number(sets[i].interval)
      }
      return totalWorkoutTime
    }
  },
  created () {
    let quickPlans = localStorage.getItem('workoutCountQuickPlans')
    if (quickPlans) {
      this.quickPlans = JSON.parse(quickPlans);
    }
  },
  watch: {
    'trainingForm.sets': {
      handler (newValue, oldValue) {
        let totalTime = 0;
        for (let i = 0; i < newValue.length; i++) {
          totalTime += newValue[i].times * newValue[i].interval + newValue[i].rest
        }
        this.totalTime = totalTime;
      },
      deep: true
    }
  },
  methods: {
    toggleBreakMusic () {
      this.$refs[this.breakMusic].pause();
      let i = Number(this.breakMusic.charAt(this.breakMusic.length - 1));
      let n = (i + 1) % 3
      console.log(this.breakMusic)
      this.breakMusic = 'break_music' + n;
      this.$refs[this.breakMusic].play();
      this.playingMusic = true;
    },
    stopMusic () {
      this.playingMusic = false;
      this.$refs[this.breakMusic].pause();
    },
    formatTime (time) {
      let min = Math.floor(time / 60);
      let sec = time % 60;
      let minStr = min > 0 ? min + ' 分 ' : ''
      return minStr + sec + ' 秒 '
    },
    removeSet (item) {
      let index = this.trainingForm.sets.indexOf(item);
      if (index !== -1) {
        this.trainingForm.sets.splice(index, 1);
      }
    },
    removeAllSets () {
      this.$set(this.trainingForm, 'sets', [])
    },
    duplicateSet (item) {
      this.trainingForm.sets.push(JSON.parse(JSON.stringify(item)))
    },
    savePlan () {
      let name = prompt('请输入快捷训练计划的名称', '');
      if (name !== null && name !== '') {
        let sets = JSON.parse(JSON.stringify(this.trainingForm.sets));
        this.quickPlans.push({name, sets})
        localStorage.setItem('workoutCountQuickPlans', JSON.stringify(this.quickPlans))
        this.$set(this.trainingForm, 'sets', [])
      }
    },
    saveAsPlan (plan, index) {
      debugger
      let name = plan.name;
      let sets = JSON.parse(JSON.stringify(this.trainingForm.sets));
      this.quickPlans.splice(index, 1, {name, sets})
      localStorage.setItem('workoutCountQuickPlans', JSON.stringify(this.quickPlans))
      this.popoverVisible = false;
      this.$set(this.trainingForm, 'sets', [])
    },
    selectPlan (plan) {
      this.$set(this.trainingForm, 'sets', JSON.parse(JSON.stringify(plan.sets)));
    },
    deletePlan (index) {
      this.quickPlans.splice(index, 1)
      localStorage.setItem('workoutCountQuickPlans', JSON.stringify(this.quickPlans))
    },
    addSet () {
      this.trainingForm.sets.push({
        name: '动作' + (this.trainingForm.sets.length + 1),
        times: 30,
        interval: 2,
        rest: 0
      });
    },
    startTraining () {
      let _this = this;
      _this.finished = false;
      _this.activeSet = 0;
      _this.trainingDialogVisible = true;
      let sets = this.trainingForm.sets;
      async function playSets (sets) {
        for (let i = 0; i < sets.length; i++) {
          _this.activeSet = i
          _this.currentTick = undefined;
          _this.isTraining = false;
          await _this.playSet(sets[i], i)
        }
        _this.finished = true;
        _this.$refs.end.play();
      }
      playSets(sets);
    },
    playSet (set, i) {
      let _this = this;
      return new Promise((resolve, reject) => {
        let startAudio = _this.$refs.start;
        let restAudio = _this.$refs.rest;
        let breakMusicAudio = _this.$refs[this.breakMusic];
        let rest = Number(set.rest)
        let setNum = i + 1
        _this.playAudio(_this.$refs.setStart).then(() => {
          _this.playNumAudio(setNum).then(() => {
            _this.playAudio(_this.$refs.setEnd).then(() => {
              _this.playAudio(startAudio).then(() => {
                let times = Number(set.times)
                let interval = Number(set.interval)
                _this.isTraining = true;
                let countDownNum = 10;
                let encourage = (times >= 80);
                _this.currentTick = times
                _this.playTicks(times, interval, countDownNum, encourage, true).then(() => {
                  if (rest > 0 && setNum < _this.trainingForm.sets.length) {
                    _this.isResting = true;
                    _this.currentTick = rest;
                    _this.playAudio(restAudio).then(() => {
                      breakMusicAudio.play();
                      _this.playTicks(rest, 1, countDownNum).then(() => {
                        breakMusicAudio.pause();
                        _this.isResting = false;
                        resolve()
                      })
                    })
                  } else {
                    resolve();
                  }
                });
              })
            })
          })
        })
      })
    },
    playTicks (times, interval, countDownNum = 0, encourage = false, playTick = false) {
      let _this = this;
      let half = Math.floor(times / 2);
      let quarter = Math.floor(times / 4);
      let tickAudio = this.$refs.tick;
      return new Promise((resolve, reject) => {
        function tick (times) {
          _this.currentTick = times
          times--;
          if (times >= 0) {
            if (playTick) {
              tickAudio.play()
            }
            if (encourage) {
              if (times === quarter) {
                _this.$refs['encourage1'].play()
              } else if (times === half) {
                _this.$refs['encourage0'].play()
              }
            }
            if (countDownNum && times < countDownNum) {
              _this.playNumAudio(times + 1)
            }
            _this.timer = setTimeout(() => {
              tick(times)
            }, interval * 1000)
          } else {
            clearTimeout(_this.timer);
            resolve()
          }
        }
        tick(times);
      })
    },
    playAudio (el) {
      return new Promise((resolve, reject) => {
        el.play();
        setTimeout(() => {
          resolve();
        }, Math.floor(el.duration * 1000))
      })
    },
    playNumAudio (num) {
      let _this = this;
      return new Promise((resolve, reject) => {
        if (num > 10) {
          let tens = 'num' + Math.floor(num / 10) + '0'
          let ones = 'num' + Math.floor(num % 10)
          _this.playAudio(_this.$refs[tens]).then(() => {
            _this.playAudio(_this.$refs[ones]).then(() => {
              resolve()
            })
          })
        } else {
          let numStr = 'num' + num;
          _this.playAudio(_this.$refs[numStr]).then(() => {
            resolve()
          })
        }
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  .music-btns {
    position: absolute;
    right: 10px;
    top: 10px;
    z-index: 1000;
    width: 35px;
    .stop-btn {
      margin-left: 0;
      margin-top: 10px;
    }
  }
  .audios {
    height: 0;
  }
  .plan-item {
    position: relative;
    display: inline-block;
    margin-left: 0;
    margin-right: 40px;
    margin-bottom: 20px;
    .del-btn {
      position: absolute;
      margin: 0;
      right: -15px;
      top: -15px;
    }
  }
  .training-form {
    padding: 15px 0;
    min-width: 350px;
    .el-form-item__label {
      font-weight: bold;
    }
    .set-name {
      width: 130px;
    }
    .set-item {
      display: inline-block;
      max-width: 240px;
      .set-label {
        display: inline-block;
        text-align: right;
        width: 60px;
      }
    }
  }

  .training-dialog {
    text-align: center;
    .el-dialog__footer {
      text-align: center;
    }
    .training-progress {
      width: 100%;
      height: 6px;
      line-height: 6px;
      display: flex;
      justify-content: space-between;
      position: absolute;
      top: 0;
      left: 0;
      .progress-item {
        background: rgba(233, 233, 235, 0.5);
        &+.progress-item {
          border-left: 6px solid #fff;
        }
        .done-bar {
          display: block;
          width: 100%;
          height: 6px;
          background: rgba(233, 233, 235, 0.5);
          animation-fill-mode: forwards;
          &.done {
            background: orange;
            width: 100%;
          }
        }
      }
    }
    .current-set-info {
      .set-name {
        font-size: 30px;
        font-weight: bold;
        line-height: 50px;
      }
      .remaining-times,.remaining-rest{
        border-radius: 50%;
        width: 200px;
        height: 200px;
        font-size: 80px;
        line-height: 200px;
        margin: 80px 0;
        display: inline-block;
        background: blueviolet;
        color: white;
      }
      .remaining-rest {
        position: relative;
        overflow: hidden;
        font-size: 28px;
        animation: glimmer 2s linear infinite alternate;
      }
      .resting-progress {
        height: 200px;
        width: 200px;
        z-index: -1;
        margin: 0;
        background: #409EFF;
        position: absolute;
        bottom:0;
        left: 0;
      }
      .operation-btns {
        position: absolute;
        bottom: 100px;
        width: 100%;
      }
      .pause-btn {
        padding: 0;
        width: 40px;
        height: 40px;
        line-height: 40px;
        font-size: 30px;
        text-align: center;
      }
    }
    .finished {
      .picture {
        font-size: 200px;
        line-height: 300px;
      }
      .text {
        font-size: 30px;
        line-height: 50px;
      }
    }
  }

  /deep/ .el-dialog__footer {
    text-align: center;
  }
</style>



