<script>
import { shuffle } from '@/utils/arr'

export default {
  name: 'Beats',
  data () {
    return {
      isRecording: false,
      playingAudio: null,
      records: [],
      recordingTime: null,
      totalTime: '',
      activeCardName: '44',
      recording: false,
      phaseOneBeats: '4，4，8，16',
      phaseTwoBeats: '4，8，前8后16，前16后8',
      Interval: null
    }
  },
  computed: {
  },
  created () {
    let trainedTime = localStorage.getItem('MusicTrainedTimeRecords');
    if (trainedTime) {
      trainedTime = trainedTime.split('|');
      if (trainedTime[0] !== new Date().toLocaleDateString()) {
        localStorage.removeItem('MusicTrainedTimeRecords');
      } else {
        this.records = JSON.parse(trainedTime[1]);
        // 第一条不是今天的，就全部刷新掉
        if (new Date(this.records[0].start).toLocaleDateString() === trainedTime[0]) {
          this.records.forEach((current) => {
            current.start = new Date(current.start);
            current.end = new Date(current.end);
          });
          this.getTotalTime();
        } else {
          localStorage.removeItem('MusicTrainedTimeRecords');
        }
      }
    }
  },
  methods: {
    play (e) {
      if (this.playingAudio) {
        this.playingAudio.pause();
      } else {
        if (this.isRecording) {
          this.isRecording = false;
          this.endNewRecord();
        }
      }
      setTimeout(() => {
        this.isRecording = true;
        this.playingAudio = e.target;
        this.startNewRecord();
      }, 500)
    },
    stop () {
      this.playingAudio = null;
      this.endNewRecord();
    },
    recordManual () {
      if (this.isRecording) {
        this.isRecording = false;
        if (this.playingAudio) {
          this.playingAudio.pause();
          this.playingAudio = null;
        } else {
          this.endNewRecord();
        }
      } else {
        this.startNewRecord();
      }
    },
    startNewRecord () {
      this.isRecording = true;
      this.records.push({
        start: new Date(),
        end: null
      });
      this.Interval = setInterval(() => {
        let record = {
          start: this.records[this.records.length - 1].start,
          end: new Date()
        };
        this.recordingTime = this.getTime(record);
      }, 1000);
    },
    endNewRecord () {
      this.isRecording = false;
      let record = this.records[this.records.length - 1];
      let end = new Date();
      let minRecordTime = 60 * 1000;
      if (end.getTime() - record.start > minRecordTime) {
        record.end = end;
      } else {
        this.records.pop();
      }
      localStorage.setItem('MusicTrainedTimeRecords', new Date().toLocaleDateString() + '|' + JSON.stringify(this.records));
      this.getTotalTime();
      clearInterval(this.Interval);
      this.recordingTime = null;
    },
    getTime (record) {
      let time = Math.floor((record.end.getTime() - record.start.getTime()) / 1000);
      let min = Math.floor(time / 60);
      let sec = time % 60;
      let hour = 0;
      if (min > 60) {
        hour = Math.floor(min / 60);
        min = min % 60;
      }
      let hourText = '';
      if (hour > 0) {
        hourText = hour + ' hours ';
      }
      return hourText + min + ' min ' + sec + ' seconds ';
    },
    getShowTime (date) {
      return date.getHours() + ':' + date.getMinutes() + ':' + date.getSeconds();
    },
    getTotalTime () {
      let hourText = '';
      let min = 0;
      let sec = 0;
      if (this.records.length) {
        let total = this.records.reduce((accumulator, currentValue) => {
          let end = currentValue.end ? currentValue.end : new Date();
          let time = end.getTime() - currentValue.start.getTime();
          return accumulator + time;
        }, 0);
        total = Math.floor(total / 1000);
        min = Math.floor(total / 60);
        sec = total % 60;
        let hour = 0;
        if (min > 60) {
          hour = Math.floor(min / 60);
          min = min % 60;
        }
        if (hour > 0) {
          hourText = hour + ' hours ';
        }
      }
      this.totalTime = hourText + min + ' min ' + sec + ' seconds ';
    },
    getRandomBeatOne () {
      let result = shuffle(this.phaseOneBeats.split('，')).join('，');
      this.$set(this.$data, 'phaseOneBeats', result);
    },
    getRandomBeatTwo () {
      let result = shuffle(this.phaseTwoBeats.split('，')).join('，');
      this.$set(this.$data, 'phaseTwoBeats', result);
    },
    refresh () {
      window.location.reload();
    }

  }
}
</script>

