import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/index'
import Beats from '@/components/Beats'
import Stave from '@/components/stave'
import Scale from '@/components/scale'
import WorkoutCount from '@/components/workout/count'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: Index
    },
    {
      path: '/beats',
      name: 'beats',
      component: Beats
    },
    {
      path: '/stave',
      name: 'stave',
      component: Stave
    },
    {
      path: '/scale',
      name: 'scale',
      component: Scale
    },
    {
      path: '/workout/count',
      name: 'workoutCount',
      component: WorkoutCount
    }
  ]
})



