<template>
  <div class="stave-wrap">
    <div class="clef"><img :src="trebleClef" /></div>
    <div class="stave-box">
      <div class="note"></div>
    </div>
  </div>
</template>

<script>
import trebleClef from '@/assets/images/treble_clef.png'
export default {
  name: 'stave',
  data () {
    return {
      trebleClef
    }
  }
}
</script>

<style scoped>
  .stave-wrap {
    margin-top: 55px;
    position: relative;
  }
  .clef {
    position: absolute;
    top: -24px;
    left: -20px;
    width: 50px;
    height: 100px;
    z-index: 50;
  }
  .clef img {
    height: 90px;
  }
  .stave-box {
    background: repeating-linear-gradient(#000 0,white 2px,white 12px);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    padding-left: 80px;
    height: 55px;
  }
  .note {
    position: absolute;
    width: 8px;
    height: 6px;
    border-radius: 100%;
    border: 2px solid #000;
    margin-top: 2px;
  }
</style>



